<script>
import Vinput from '../components/Vinput'
export default {
  name: 'Edit',
  data: function () {
    return {
      testText: '<span  style="color:red;">USD</span>',
      islogin: false,
      isloading: false,
      password: '',
      formItem: {
        title: '',
        remark: '',
        speech: {
          userinfo: {},
          weixin: [],
          startmsg: [],
          problem: []
        }
      }
    }
  },
  components:{
    "v-input":Vinput
  },
  created: function () {
    var that = this
    if (localStorage.login === '') {
      this.islogin = true
      return
    }
    this.$ajax({
      method: 'get',
      url: 'https://wechat.shangning.site/index/test/LinkDetail/id/' + that.$route.params.id
    }).then(function (res) {
      if (res.data.list.speech !== null & res.data.list.speech !== '') {
        var tbox = document.createElement('div')
        tbox.innerHTML = res.data.list.speech
        // console.log(tbox.innerHTML)
        res.data.list.speech = JSON.parse(res.data.list.speech)
        that.formItem = res.data.list
        that.$forceUpdate()
      } else {
        that.formItem = res.data.list
        that.formItem.speech = {
          userinfo: {},
          weixin: [],
          startmsg: [],
          problem: []
        }
        that.$forceUpdate()
      }
    })
  },
  methods: {
    saveUrl: function () {
      var that = this
      if (localStorage.login === '' || localStorage.login === undefined) {
        this.islogin = true
        this.$Message.warning('')
        return
      }
      if (this.formItem.title === '') {
        return this.$Message.warning('')
      }
      that.isloading = true
      var data = JSON.parse(JSON.stringify(this.formItem))
      data.speech = JSON.stringify(this.formItem.speech)

      this.$ajax({
        method: 'post',
        url: 'https://wechat.shangning.site/index/test/updateMessage/id/' + this.$route.params.id,
        data: data
      }).then(function (res) {
        that.isloading = false
        console.log(res)
        if (res.data.status === 1) {
          that.$Message.success('')
          window.location.href = ''
          return
        }
        that.$Message.success('')
      })
    },
    startLogin: function () {
      if (this.password !== 'sn20170529') {
        this.password = ''
        this.$Message.error('')
        return
      }
      localStorage.login = 'true'
      window.location.reload()
    },
    upHeadPicExceededSize () { // 20M
      this.$Message.warning('20M')
    },
    upHeadPicSuccess (res) {
      var pic = 'https://wechat.shangning.site' + res.url
      console.log(this.formItem.speech.userinfo.headpic)
      this.formItem.speech.userinfo.headpic = pic
      this.$forceUpdate()
    },
    uploadOne (response, file, fileList, data) {
      var pic = 'https://wechat.shangning.site' + response.url
      this.formItem.speech.startmsg[data.index].content = pic
      this.$forceUpdate()
    },
    uploadTwo (response, file, fileList, data) {
      var pic = 'https://wechat.shangning.site' + response.url
      this.formItem.speech.weixin[data.index].wxpic = pic
      this.$forceUpdate()
    },
    addStartMsg () {
      this.formItem.speech.startmsg.push({
        send_type: 1001,
        type: 'text',
        content: ''
      })
      this.$forceUpdate()
    },
    addWeiXin () {
      this.formItem.speech.weixin.push({
        wxnum: '',
        wxpic: ''
      })
      this.$forceUpdate()
    },
    delWeiXin (index) {
      this.formItem.speech.weixin.splice(index, 1)
      this.$forceUpdate()
    },
    delStartMsg (index) {
      this.formItem.speech.startmsg.splice(index, 1)
      this.$forceUpdate()
    },
    changeMsgType (index, value) {
      if (value === 'text') {
        this.formItem.speech.startmsg[index].content = ''
      } else if (value === 'image') {
        this.formItem.speech.startmsg[index].content = ''
      } else if (value === 'video') {
        this.formItem.speech.startmsg[index].content = {}
      }
    },
    addProblemDesc (index) {
      this.formItem.speech.problem[index].desc.push('')
      this.$forceUpdate()
    },
    delProblemDesc (index) {
      this.formItem.speech.problem[index].desc.splice(this.formItem.speech.problem[index].desc.length - 1, 1)
      this.$forceUpdate()
    },
    addProblemOpt (index) {
      this.formItem.speech.problem[index].options.push({
        title: '',
        reply: [],
        againask: true
      })
      this.$forceUpdate()
    },
    delProblemOpt (index, index1) {
      this.formItem.speech.problem[index].options.splice(index1, 1)
      this.$forceUpdate()
    },
    addProblemOptReMsg (index, index1) {
      this.formItem.speech.problem[index].options[index1].reply.push('')
      this.$forceUpdate()
    },
    delProblemOptReMsg (index, index1) {
      this.formItem.speech.problem[index].options[index1].reply.splice(this.formItem.speech.problem[index].options[index1].reply.length - 1, 1)
      this.$forceUpdate()
    },
    addProblem () {
      this.formItem.speech.problem.push({
        desc: [],
        options: []
      })
      this.$forceUpdate()
    },
    delProblem (index) {
      this.formItem.speech.problem.splice(index, 1)
      this.$forceUpdate()
    },
    onCopyError () {
      this.$Message.error('')
    },
    onCopy () {
      this.$Message.success('')
    }
  }
}
</script>
