<div class="layout">
        <Layout>
            <Header>
                <Menu mode="horizontal" theme="dark" active-name="3">
                    <div class="layout-logo"></div>
                    <div class="layout-nav">
                        <MenuItem name="1">
                            <router-link to="/"><Icon type="navicon-round"></Icon></router-link>
                        </MenuItem>
                        <MenuItem name="2">
                            <router-link to="/add"><Icon type="plus"></Icon></router-link>
                        </MenuItem>
                    </div>
                </Menu>
            </Header>
            <Content :style="{padding: '0 50px'}">
                <Breadcrumb :style="{margin: '20px 0'}">
                    <BreadcrumbItem></BreadcrumbItem>
                </Breadcrumb>
                <Card>
                    <div style="min-height: 200px;">
                        <div class="opt" style="margin:5px 0">
                          <Button type="primary" size="large" @click="exportData()"><Icon type="ios-download-outline"></Icon></Button>
                        </div>
                        <Table border :columns="columns7" :data="data6" :loading="tableLogin" ref="table"></Table>
                    </div>
                </Card>
            </Content>
            <Footer class="layout-footer-center">2011-2016 &copy; TalkingData</Footer>
        </Layout>

    </div>
</template>

<script>
export default {
  name: 'Show',
  data: function () {
    return {
      tableLogin: true,
      columns7: [
        {
          title: 'ID',
          key: 'id',
          align: 'center',
          width: 100
        },
        {
          title: 'MID',
          key: 'mid',
          align: 'center',
          width: 60
        },
        {
          title: '',
          key: 'url',
          align: 'center',
          width: 400,
          render: (h, params) => {
            if (params.row.url === 'https://www.baidu.com') {
              return h('span', '')
            }
            return h('a', {
              props: {
                target: '_blank',
                href: params.row.url
              }
            }, params.row.url)
          }
        },
        {
          title: '',
          key: 'message',
          align: 'center'
        },
        {
          title: '',
          key: 'create_time',
          align: 'center',
          width: 200,
          sortable: true,
          render: (h, params) => {
            // console.log(this.formatDate(params.row.create_time, 'yyyy-MM-dd hh:mm:ss'))
            return h('span', this.formatDate(params.row.create_time, 'yyyy-MM-dd hh:mm:ss'))
          }
        },
        {
          title: 'Action',
          key: 'action',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h(
                'Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      // this.showUrl = true
                      this.$Message.warning('')
                    }
                  }
                },
                'View'
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      // this.remove(params.index)
                      this.$Message.warning('')
                    }
                  }
                },
                'Delete'
              )
            ])
          }
        }
      ],
      data6: [
        // {
        //   id: 1,
        //   title: '',
        //   createtime: '20170405 24:24:24'
        // }
      ]
    }
  },
  created: function () {
    var that = this
    this.$ajax({
      method: 'get',
      url: 'https://wechat.shangning.site/index/test/showMessageList?id=' + that.$route.params.id
    }).then(function (res) {
      that.tableLogin = false
      // console.log(res.data)
      // console.log(res.data.list)
      that.data6 = res.data.list
    })
  },
  methods: {
    exportData () {
      this.$refs.table.exportCsv({
        filename: ''
      })
    },
    formatDate (date, fmt) {
      date = new Date(date.toString().length > 10 ? date : date * 1000)
      if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
      }
      let o = {
        'M+': date.getMonth() + 1,
        'd+': date.getDate(),
        'h+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds()
      }
      for (let k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
          let str = o[k] + ''
          fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length))
        }
      }
      return fmt
    }
  }
}
</script>
