import Vue from 'vue'
import Router from 'vue-router'
import List from './views/List.vue'
import Show from './views/Show.vue'
import Add from './views/Add.vue'
import Edit from './views/Edit.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'list',
      component: List
    }, {
      path: '/add',
      name: 'add',
      component: Add
    }, {
      path: '/add/:id',
      name: 'add',
      component: Add
    }, {
      path: '/show/:id',
      name: 'show',
      component: Show
    }, {
      path: '/edit/:id',
      name: 'edit',
      component: Edit
    }
  ]
})
