<template>
  <vue-wangeditor :menus="menus" id="editor" ref="editor" v-model="innerText" @change="textInput" :width="width" :height="height"></vue-wangeditor>
</template>

<script>
import vueWangeditor from 'vue-wangeditor'
export default {
  name: 'Vinput',
  data: function () {
    return {
      innerText:this.value,
      menus: [
        'forecolor',	// 
        'bgcolor',	// 
        'emotion',	// 
        'underline',	// 
        'strikethrough',	// 
        'italic',	// 
        'bold',	// 
        'link'	// 
      ]
    }
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    height:{
      type: String,
    },
    width:{
      type: String,
    }
  },
  components: {
    vueWangeditor
  },
  methods:{
    textInput:function(e){
      this.innerText = this.$refs.editor.getHtml();
      this.$emit('input',this.innerText)
    }
  }
}
</script>

