<template>
<div class="layout">
      <Modal v-model="islogin" width="360" :closable="false" :mask-closable="false">
        <p slot="header" style="color:#000;text-align:center">
            <span></span>
        </p>
        <div style="text-align:center">
            <Input type="password" v-model="password" placeholder="..." style="width: 300px" />
        </div>
        <div slot="footer">
            <Button type="primary" size="large" @click="startLogin" long></Button>
        </div>
    </Modal>
        <Layout>
            <Header>
                <Menu mode="horizontal" theme="dark" active-name="1">
                    <div class="layout-logo"></div>
                    <div class="layout-nav">
                        <MenuItem name="1">
                            <router-link to="/"><Icon type="navicon-round"></Icon></router-link>
                        </MenuItem>
                        <MenuItem name="2">
                            <router-link to="/add"><Icon type="plus"></Icon></router-link>
                        </MenuItem>
                    </div>
                </Menu>
            </Header>
            <Content :style="{padding: '0 50px'}">
                <Breadcrumb :style="{margin: '20px 0'}">
                    <BreadcrumbItem></BreadcrumbItem>
                </Breadcrumb>
                <Card>
                    <div style="min-height: 200px;">
                        <Table border :columns="columns7" :data="data6" :loading="tableLogin"></Table>
                    </div>
                </Card>
            </Content>
            <Footer class="layout-footer-center">2011-2016 &copy; TalkingData</Footer>
        </Layout>
            <Modal v-model="showUrl" width="360">
              <p slot="header" style="color:#000;text-align:center">
                  <span></span>
              </p>
              <div style="text-align:center">
                  <p><router-link target="_blank" :to="'/show/'+showURL_id">{{originUrl+'#/show/'+showURL_id}}</router-link></p>
              </div>
              <div slot="footer">
                  <Button type="primary" size="large" long @click="showUrl=false"></Button>
              </div>
          </Modal>
    </div>
</template>

<script>
export default {
  name: 'Index',
  data: function () {
    return {
      islogin: false,
      password: '',
      originUrl: location.origin + location.pathname,
      showUrl: false,
      showURL_id: null,
      tableLogin: true,
      columns7: [
        {
          title: 'ID',
          key: 'id',
          align: 'center',
          width: 60
        },
        {
          title: '',
          key: 'title',
          align: 'center',
          width: 400
        },
        {
          title: '',
          key: 'create_time',
          align: 'center',
          width: 200
        },
        {
          title: '',
          key: 'remark',
          align: 'center'
        },
        {
          title: 'Action',
          key: 'action',
          width: 230,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h(
                'Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.showURL_id = params.row.id
                      this.showUrl = true
                    }
                  }
                },
                'View'
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'info',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.$router.push({path: '/add/' + params.row.id})
                    }
                  }
                },
                'Copy'
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'warning',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.$router.push({path: '/edit/' + params.row.id})
                    }
                  }
                },
                'Edit'
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                },
                'Delete'
              )
            ])
          }
        }
      ],
      data6: [
        // {
        //   id: 1,
        //   title: '',
        //   createtime: '20170405 24:24:24'
        // }
      ]
    }
  },
  created: function () {
    var that = this
    if (localStorage.login === '' || localStorage.login === undefined) {
      this.islogin = true
      return
    }
    this.$ajax({
      method: 'get',
      url: 'https://wechat.shangning.site/index/test/LinkList'
    }).then(function (res) {
      that.tableLogin = false
      console.log(res.data.list)
      that.data6 = res.data.list
    })
  },
  methods: {
    startLogin: function () {
      if (this.password !== 'sn20170529') {
        this.password = ''
        this.$Message.error('')
        return
      }
      localStorage.login = 'true'
      window.location.reload()
    }
  }
}
</script>
